import app from '@/common/app.js'; //公共变量
import mSearch from '@/components/mehaotian-search/mehaotian-search.vue';
import request from '@/common/request.js';
import '@/static/css/index.css';
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
import loadcache from '@/common/loadcache.js';
import {
    mapState
} from 'vuex';

export default {
    components: {
        mSearch,
        uniLoadMore
    },
    data() {
        return {
            typeArray: [{
                label: "全部",
                value: ""
            }],
            typeIndex: 0,
            dateArray: [{
                label: "正序",
                value: "asc"
            }, {
                label: "逆序",
                value: "desc"
            }],
            dateIndex: 0,
            assList: [],
            mode: 'nav',
            tab_type: 0,
            status: "more",
            contentText: {
                contentdown: '加载更多',
                contentrefresh: '加载中',
                contentnomore: '没有更多'
            },
            pageNo: 0,
            pageSize: 10,
            imagePath: app.imagePath,
            detailflag: false,
            isLogin: false,
            searchType: '',
            orderType: '',
            searchName: '',
            disabled: false
        }
    },
    computed: {
        ...mapState(['hasLogin'])
    },
    onReachBottom() {
        //请求(生产环境需要判断是捐赠还是一起捐)
        this.status = "loading";
        if (this.detailflag) {
            this.getAssList();
        } else {
            this.status = "noMore";
        }
    },
    onLoad() {
        this.isLogin = this.hasLogin;
        loadcache.getDictsByTypeCode(app.DICT_TYPECODE.ASSOCIATION_TYPE).forEach((d) => {
            this.typeArray.push(d);
        });
    },
    onShow() {
        this.assList = [];
        this.pageNo=0;
        this.getAssList();
    },
    methods: {
        getAssList() {
            this.pageNo = this.pageNo + 1;
            request.http({
                url: '/app/as/getAssociations',
                header: 'application/x-www-form-urlencoded',
                method: "POST",
                data: {
                    pageNo: this.pageNo,
                    pageSize: this.pageSize,
                    type: this.searchType,
                    orderType: this.orderType,
                    name: this.searchName
                },
                success: (res) => {
                    if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                        if (res.data.list == undefined || res.data.list.length == 0) {
                            this.status = "noMore";
                            setTimeout(() => {
                                this.showLoadMore = false;
                            }, 500);
                        } else {
                            var datas = res.data.list;
                            datas.forEach((d) => {
                                if (d.type != null) {
                                    d.type = loadcache.getDictByvalue(d.type, app.DICT_TYPECODE
                                        .ASSOCIATION_TYPE).label;
                                }
                                this.assList.push(d);
                            });
                        }
                        if (res.data.list.length < this.pageSize) {
                            this.status = "noMore";
                        }
                        this.detailflag = this.pageNo >= res.data.pages ? false : true;
                        this.disabled = false;

                    }
                },
                error: (res) => {

                }
            });
        },
        goDetail(e) {
            var url = '/pages_mine/myAssociation/myAssociationDetail?id=' + encodeURIComponent(JSON.stringify(e.id));
            uni.navigateTo({
                url
            });
        },
        bindChangeType(value, e) {
            this.assList = [];
            this.pageNo = 0;
            this.searchType = value[e.target.value].value;
            this.getAssList();
        },
        bindChangeDate(value, e) {
            this.assList = [];
            this.pageNo = 0;
            this.orderType = value[e.target.value].value;
            this.getAssList();
        },
        search(e, val) {
            if (this.disabled) {
                return false;
            }
            this.disabled = true;
            this.assList = [];
            this.searchName = e;
            this.pageNo = 0;
            this.getAssList();
        },
        showpageToast(msg) {
            uni.showToast({
                icon: 'none',
                title: msg
            });
        },
        goMyAss(e) {
            //这块还需要判断是否能够发起值年返校
            if (this.isLogin) {
                let url = '/pages_mine/myAssociation/myAssociation';
                uni.navigateTo({
                    url: url
                });
            } else {
                let url = '/pages/login/login';
                uni.navigateTo({
                    url: url
                });
                this.showpageToast("您还未登录，不能发起值年返校");
            }

        },
    }
}
